<script async setup>
  import { useCompanyListConfigStore, useCompanyStore } from '@/stores/company'
  import SearchForm from './SearchForm.vue'
  const props = defineProps({})
  const formConfigStore = useCompanyListConfigStore()
  const companyStore = useCompanyStore()

  const defaultParams = {
    search: '',
    sortBy: 'name',
    sortOrder: 'asc',
    currentPage: 1,
    pageSize: 10,
  }

  const params = computed(() => {
    return { ...defaultParams, ...props }
  })
  const columnsDef = computed(() => {})
  async function preload() {
    await companyStore.loadCompanies(params)
  }
  const { collection: result } = storeToRefs(companyStore)
  await preload()
</script>
<template>
  <h1>Liste des enseignes</h1>
  <SearchForm :configStore="formConfigStore" />
  <PaginationWidget
    :hits="result.hits"
    :totalPages="result.totalPages"
    :totalHits="result.totalHits"
    v-model:itemsPerPage="params.items_per_page"
    v-model:page="params.page"
    v-model:columns="params.columns"
    :columns-def="columnsDef"
  />
</template>
