<script setup>
  import { Suspense, ref } from 'vue'
  import { collectOptions } from '@/helpers/context.js'
  import { useCompanyListConfigStore } from '@/stores/company'

  import CompanyListComponent from './CompanyListComponent.vue'

  const options = collectOptions()

  const loading = ref(true)
  const formConfigStore = useCompanyListConfigStore()
  formConfigStore.setUrl(options.form_config_url)

  const companyStore = useCompanyListConfigStore()

  async function preload() {
    const promises = [formConfigStore.loadConfig()]
    promises.push(companyStore.loadCompanies())

    Promise.all(promises).then(() => (loading.value = false))
  }
  preload()
</script>

<template>
  <Suspense>
    <div>
      <div v-if="loading">Chargement des informations</div>

      <CompanyListComponent v-else :result="companies"/>
    </div>
    <template #fallback> Chargement... </template>
  </Suspense>
</template>
